// src/redux/store.js
import { createStore, combineReducers, applyMiddleware } from 'redux';
import setReducer from './redux/reducers/setReducer';
import tokenReducer from './redux/reducers/tokenReducer';
import thunk from 'redux-thunk'; // If you want to use asynchronous actions

const rootReducer = combineReducers({
  set: setReducer,
  token: tokenReducer,
});

// Add any middleware you need, such as thunk for handling async actions
const middleware = [thunk];

const store = createStore(rootReducer, applyMiddleware(...middleware));

export default store;
